import React from 'react';
import type { TextProps as ExternalTextProps } from 'external/src/typography/Text';
import type { TextProps as CoreTextProps } from 'core/src/typography/Text/types';

type WrappedComponentProps = ExternalTextProps | CoreTextProps;

/** TODO подумать над переименование данного пропса,
 * в связи с тем, что есть схожий по неймингу, но отличный по смыслу - inheritColor
 * https://jira.corp.tass.ru/browse/TNA-788
 */
interface WithInheritProps {
  /** Флаг для использования наследуемых стилей вместо стилей по умолчанию */
  inherit?: boolean;
}

const DEFAULT_PROPS = {
  inherit: false
};

function withInherit<T extends WrappedComponentProps>(
  WrappedComponent: React.ComponentType<T & WithInheritProps>
): React.FC<T & WithInheritProps> {
  const displayName = WrappedComponent.displayName || WrappedComponent.name || 'Component';
  const ComponentWithInherit: React.FC<T & WithInheritProps> = props => {
    const {
      inherit,
      fontWeight,
      color,
      size,
      ...rest
    } = props;

    if (inherit) {
      const overriddenProps = {
        ...rest,
        fontWeight: fontWeight ?? 'inherit',
        color: color ?? 'inherit',
        size: size ?? 'inherit'
      } as T;

      return (
        <WrappedComponent
          { ...overriddenProps }
        />
      );
    }

    return (
      <WrappedComponent
        { ...props as T }
      />
    );
  };
  ComponentWithInherit.displayName = `withInherit(${ displayName })`;

  return ComponentWithInherit;
}

export {
  withInherit,
  WithInheritProps,
  WrappedComponentProps,
  DEFAULT_PROPS
};
